import { useState } from "react";

import Image from "./Image";

import Godzilla from "../data/images/godzilla-1954-png-Transparent-Images.png";
import Rodan from "../data/images/rodan_1964_transparent_ver_2_by_jacksondeans_dd3cicy-fullview.png";
import Anguirus from "../data/images/16-162890_free-render-for-use-anguirus-1972-toy-hd.png";

export default function AnswerPicker(props) {
  const images = [Godzilla, Rodan, Anguirus];
  const [answerIndex, setAnswerIndex] = useState(0);

  function answerNavForward() {
    setAnswerIndex((answerIndex + 1) % images.length);
  }

  return (
    <div>
      Answer
      <br />
      {/*<img src={props.imageUrl} alt={props.imageAlt} />*/}
      <Image src={images[answerIndex]} />
      <br />
      <button onClick={answerNavForward}>Next</button>
    </div>
  );
}
